import './App.css';
import { Switch, Route } from 'react-router-dom';
import Header from "./Components/Header/Header"
import Home from './Page/Home/Home';
import Subscriptions from './Page/Subscriptions/Subscriptions';
import Trending from './Page/Trending/Trending';
import Menu from './Components/Menu/Menu';
import Library from './Page/Library/Library';
import History from './Page/History/History';
import Watch from "./Page/Watch/Watch";
import Favourite from "./Page/Favourite/Favourite";
import Like from "./Page/Like/Like";
import Music from "./Page/Music/Music";
import Game from "./Page/Game/Game";
import Show from "./Page/Show/Show";
import Setting from './Page/Setting/Setting';
import Chennels from './Page/Chennels/Chennels';


function App() {
  return (<>
    <Header/>
    <Menu/>
    <Switch>
      <Route path="/" component={Home} exact>
        <Home />
      </Route>
      <Route path="/trending" component={Trending} exact>
        <Trending />
      </Route>
      <Route path="/Subscriptions" component={Subscriptions} exact>
        <Subscriptions />
      </Route>
      <Route path="/library" component={Library} exact>
        <Library />
      </Route>
      <Route path="/history" component={History} exact>
        <History />
      </Route>
      <Route path="/watch" component={Watch} exact>
        <Watch />
      </Route>
      <Route path="/favourite" component={Favourite} exact>
        <Favourite />
      </Route>
      <Route path="/like" component={Like} exact>
        <Like />
      </Route>
      <Route path="/music" component={Music} exact>
        <Music />
      </Route>
      <Route path="/game" component={Game} exact>
        <Game />
      </Route>
      <Route path="/show" component={Show} exact>
        <Show />
      </Route>
      <Route path="/setting" component={Setting} exact>
        <Setting />
      </Route>
      <Route path="/channel/:videoId" component={Chennels} exact>
        <Chennels />
      </Route>
    </Switch>
    </>);
  }
  
  export default App;
  