import "./Header.scss";
import Logo from "../../svg-icons/svg-fontIcons"
import callVideo from "../../img/kamera.svg";
import allMore from "../../img/all-more.svg";
import sms from "../../img/zvanok.svg";
import profileImg from "../../img/Userpic.png";

function Header() {
   return(<>
      <Logo/>
      <header className="header">
         <div className="header__left">
            
            <input className="header__input" type="text"placeholder="Search" />
         </div>
         
         <div className="header__right">
            <button
            onClick={(e) => {
            if (document.body.className === ("light")) {
               document.body.className = "dark"
            
            } else if(document.body.className.includes("dark")){
               document.body.className = "light"
            }
            const iconSun = document.querySelector(".iconSun")
            const iconMoon = document.querySelector(".iconMoon")
            if (iconSun.className.includes("click__icon")) {
               iconSun.classList.remove("click__icon")
               iconMoon.classList.add("click__icon")

            } else if(iconMoon.className.includes("click__icon")){
               iconMoon.classList.remove("click__icon")
               iconSun.classList.add("click__icon")
         }
      }
      }
            className="icons">
               <div className="iconSun click__icon">
                  <i className='bx bxs-sun' style={{"color": "rgba(255, 164, 0, 1)",}} ></i>
               </div>
               <div className="iconMoon" >
                  <i className='bx bxs-moon' ></i>
               </div>
            </button>
            <img className="header__right--img" src={callVideo} alt="header-top imges" />
            <img className="header__right--img" src={allMore} alt="header-top imges" />
            <img className="header__right--img" src={sms} alt="header-top imges" />
            <img className="header__right--img" src={profileImg} alt="header-top imges" />
         </div>
      </header>

      </>)
   }
   
   export default Header;